<template lang="pug">
include ../../../mixins/bemto/bemto
+b.icon(
  :class="computedClasses",
  @click="clickHandler",
  @mouseover="mouseOverHandler",
  @mouseleave="mouseLeaveHandler"
)
  +e.link.A(v-if="linkUrl", :href="linkUrl", target="_blank")
</template>
<script>
export default {
  props: {
    iconClass: {
      type: String,
      default() {
        return ''
      },
    },
    isHovered: {
      type: Boolean,
      default() {
        return true
      },
    },
    linkUrl: {
      type: String,
      default() {
        return ''
      },
    },
    small: {
      type: Boolean,
      default() {
        return false
      },
    },
  },
  data: () => ({
    hoverClass: '',
  }),
  computed: {
    computedClasses() {
      const isSmall = this.small ? 'icon--small' : ''
      return `${this.iconClass} ${this.hoverClass} ${isSmall}`
    },
  },
  methods: {
    clickHandler() {
      this.$emit('click-handler')
    },
    mouseOverHandler() {
      this.$emit('mouse-over-handler')
      this.isHovered ? (this.hoverClass = 'icon--accent') : ''
    },
    mouseLeaveHandler() {
      this.$emit('mouse-leave-handler')
      this.hoverClass = ''
    },
  },
}
</script>
